/* Dashboard Content */

.dashboard-content {
    display: flex;
    gap: 5%;
    justify-content: center;
}
/* 
.dashboard-left {
    flex-basis: 40%;
}

.dashboard-right {
    flex-basis: 60%;
} */

.content-title {
    text-decoration: none;
    color: rgb(73, 96, 139);
}

.links {
    text-decoration: underline rgb(73, 96, 139);
}

.content-title.links:hover, .content-title.links h2:hover {
    text-decoration: underline rgb(54, 72, 104);
    color: rgb(54, 72, 104);
}

.center {
    text-align: center;
}

/* Owner Content */

.owner-card {
    min-width: 200px;
    margin: auto;
    box-shadow: 0 3px 7px -1px rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    background-color: white;
    padding: 20px;
    line-height: 1.5rem;
}

.owner-card h2 {
    margin-bottom: 0;
}

.owner-card h3 {
    font-weight: 400;
    margin: 1rem auto;
}

.edit-owner.card-button {
    left: 70%;
    position: relative;
}

/* Pet Scroll */

.scroll-container {
    max-width: 400px;
    padding: 0 15px;
    margin: 0 auto;
}

.scroll-container a {
    color: black;
}

.pet-scroll-cards {
    display: flex;
    /* padding: 25px 0px; */
    list-style: none;
    overflow-x: scroll;
    scroll-snap-type: x mandatory;
    margin-top: 0;
    padding-left: 0;
}

.pet-scroll-card {
    /* border-radius: 12px; */
    box-shadow: 0 5px 15px rgba(0,0,0,15%);
    scroll-snap-align: start;
    transition: all 0.8s;
}

.pet-scroll-card:not(:last-child) {
    margin-right: 10px;
}

.pet-scroll-cards::-webkit-scrollbar {
    height: 12px;
}

.pet-scroll-cards::-webkit-scrollbar-thumb,.pet-scroll-cards::-webkit-scrollbar-track {
    border-radius: 92px;
}

.pet-scroll-cards::-webkit-scrollbar-thumb {
    background-color: rgb(73, 96, 139);
}

.pet-scroll-cards::-webkit-scrollbar-track {
    background-color: #edf2f4;
}

.pet-scroll {
    max-width: 200px;
    /* max-height: 200px; */
    display: flex;
    flex-direction: column;
}

.photo {
    flex-basis: 60%;
    height: auto;
    position: relative;
}

.pet-scroll img {
    /* position: absolute; */
    /* top: 0;
    bottom: 0;
    left: 0;
    right: 0; */
    width: 200px;
    height: 200px;
    display: block;
    object-fit: cover;
    /* object-position: center; */
}

.pet-name {
    flex-basis: 40%;
    text-align: center;
}

.pet-name > * {
    margin: 0;
}

/* Appointment Scroll */

.appt-scroll-cards {
    display: flex;
    list-style: none;
    overflow-x: scroll;
    scroll-snap-type: x mandatory;
    padding-left: 0;
}

.appt-scroll-card {
    display: flex;
    padding: 10px 20px;
    background-color: white;
    border-radius: 12px;
    box-shadow: 0 5px 15px rgba(0,0,0,15%);
    scroll-snap-align: start;
    transition: all 0.4s;
    min-width: 200px;
}

.appt-scroll-card h3 {
    margin: 0 auto;
}

.appt-scroll-card p {
    margin-bottom: 0;
}

.appt-scroll-cards::-webkit-scrollbar{
    height: 12px;
}

.appt-scroll-cards::-webkit-scrollbar-thumb, .appt-scroll-cards::-webkit-scrollbar-track {
    border-radius: 92px;
}

.appt-scroll-cards::-webkit-scrollbar-thumb {
    background-color: rgb(73, 96, 139);
}

.appt-scroll-cards::-webkit-scrollbar-track {
    background-color: #edf2f4;
}

@media (max-width: 768px) {
    .dashboard-content {
        flex-direction: column;
        gap: 0;
        width: 90%;
        margin: auto;
    }

    .owner-card h3 {
        font-size: 1.5rem;
    }

    .edit-owner.card-button {
        left: 78%;
    }

    .content-title {
        text-align: center;
        margin-bottom: 1rem;
    }

    .scroll-container {
        width: 100%;
        padding: 0;
    }
}