
.container {
    width: 90%;
    margin: auto;
    margin-bottom: 5%;
}
li {
    list-style: none;
}

.title {
    text-align: center;
}

.subtitle {
    display: inline-block;
}

.apt-container {
    display: flex;
    /* margin: 10%; */
    gap: 1.5em;
    flex-wrap: wrap;
}

.apt-card {
    padding: 10px;
    text-align: center;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.473);
    height: auto;
    width: 10.5em;
}

/* modal */

.modal {
    display: none;
    position: fixed;
    z-index: 1;
    padding-top: 100px;
    left: 0;
    right: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0,0,0,0.4);
}

.modal.show {
    display: block;
}

.modal-content-container {
    background-color: white;
    margin: auto;
    /* border: solid black; */
    width: 80%;
    padding: 10px 25px;
    border-radius: 25px;
    box-sizing: border-box;
    max-height: 80vh;
    overflow: hidden;
}

.modal-content {
    height: 80%;
    /* height: 100%; */
    width: 100%;
    overflow-y: auto;
    margin: 10px 0;
}

.modal-content::-webkit-scrollbar{
    width: .4em;
}

.modal-content::-webkit-scrollbar-track{
    /* box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); */
    border-radius: 5px;
}

.modal-content::-webkit-scrollbar-thumb{
    background-color: rgba(94, 117, 160, 0.877);
    border-radius: 5px;
}

.close {
    color: #aaaaaa;
    float: right;
    font-size: 2.5rem;
    font-weight: bold;
    transition-duration: 0.4s;
    display: inline;
}

.close:hover {
    color: #000;
    text-decoration: none;
    cursor: pointer;
}

/* Form */

.modal-title{
    text-align: center;
}

input[type=time], input[type=date], input[type=password], input[type=email], input[type=string], input[type=integer], select {
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    /* resize: vertical; */
    box-sizing: border-box;
    height: 45px;
    font: inherit;
}

input::file-selector-button{
    border: none;
    background-color: rgb(102, 135, 197);
    font-size: 15px;
    padding: 8px 15px;
    border-radius: 12px;
    transition-duration: 0.4s;
    box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.473);
}

input::file-selector-button:hover {
    background-color: rgb(73, 96, 139);
    color: white;
    cursor: pointer;
  }

label {
    padding: 12px 12px 5px 0;
    display: inline-block;
}

.col {
    /* float: left; */
    width: 95%;
    margin-top: 0;
    text-align: left;
}

.full-col {
    /* float: left; */
    width: 98%;
    margin-top: 0;
    text-align: left;
}

/* form {
    padding: 20px;
} */

.row {
    width: 50%;
    margin: auto;
}

.full-row {
    width: 90%;
    margin: auto;
}

/* .small-row {
    width: 40%;
    margin: auto;
} */

.left {
    /* float: left; */
    display: inline-block;
}

.right {
    float: right;
}

.row-container {
    width: 90%;
    margin: auto;
}

#uploadedImage {
    height: 12rem;
    width: 12rem;
    object-fit: cover;
    margin: 8% auto 5% 13%;
}

@media (max-width: 768px) {
    .container {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .apt-container li {
        width: 100%;
    }

    .apt-card {
        width: 100%;
        box-sizing: border-box;
        padding: 2rem 1rem;
    }

    .apt-card > div > h3 {
        margin-top: 0;
    }

    .modal-content-container {
        padding-top: 1.5rem;
    }

    .close {
        font-size: 2.5rem;
        /* transform: translateY(-1rem); */
        /* z-index: 2; */
    }

    .modal-title {
        margin-top: 0;
        line-height: normal;
    }

    .modal-content {
        overflow-x: hidden;
        margin: auto;
        height: 85%;
    }

    #uploadedImage {
        height: 14rem;
        width: 14rem;
        margin: auto;
    }

    .row-container {
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    .row {
        width: 100%;
    }

    .col, .full-col {
        width: 100%;
    }

    label {
        font-size: 1.3rem;
    }

    input::file-selector-button {
        font-size: 1.2rem;
        margin: 0.5rem auto;
    }
}